import Axios from 'axios'
import store from '../store'
import router from '../router'
import {Message} from 'element-ui'

Axios.defaults.baseURL = 'http://192.168.6.220:8088/api/v1'
Axios.defaults.timeout = 3000
const loginUrl = '/login'
const getsiteUrl = '/site'
const getftpUrl = '/ftp'
const getmysqlUrl = '/mysql'
const gethomeUrl = '/home'
const getsoftUrl = '/soft'

Axios.interceptors.request.use(function (config) {
    let otime = localStorage.getItem('otime')
    let username = localStorage.getItem('username')
    let accesstoken = localStorage.getItem('accesstoken')
    if (otime && username && accesstoken) {
        config.headers.otime = otime;
        config.headers.username = username;
        config.headers.accesstoken = accesstoken;
    }
    return config;
}, function (error) {
    Message.error({message: '请求超时!'});
    return Promise.reject(error);
});

Axios.interceptors.response.use(function (response) {
    if (response.data.errcode == 4023 || response.data.errcode == 4024 || response.data.errcode == 4026) {
        Message.error({message: response.data.errmsg});
        localStorage.clear();
        store.dispatch('accountLogout');
        router.push({ name: "Login" });
    }
    return response;
}, function (error) {
    if (error.response.status == 400) {
        Message.error({message: error.response.data.errmsg});
    } else {
        Message.error({message: '未知错误!'});
    }
    // return Promise.reject(error);
});

export function login(params) {
    return Axios.post(loginUrl, params).then(res => res.data)
}
export function logout(params) {
    return Axios.post(loginUrl, params).then(res => res.data)
}

export function getsite(params) {
    return Axios.get(getsiteUrl, {params: params}).then(res => res.data)
}

export function delsite(params) {
    return Axios.delete(getsiteUrl, { data: params }).then(res => res.data)
}

export function addsite(params) {
    return Axios.post(getsiteUrl, params).then(res => res.data)
}

export function upsite(params) {
    return Axios.put(getsiteUrl, params).then(res => res.data)
}

export function getftp(nid) {
    return Axios.get(`${getftpUrl}?nid=${nid}`).then(res => res.data)
}

export function addftp(params) {
    return Axios.post(getftpUrl, params).then(res => res.data)
}

export function delftp(params) {
    return Axios.delete(getftpUrl, { data: params }).then(res => res.data)
}

export function upftp(params) {
    return Axios.put(getftpUrl, params).then(res => res.data)
}

export function getmysql(nid) {
    return Axios.get(`${getmysqlUrl}?nid=${nid}`).then(res => res.data)
}

export function addmysql(params) {
    return Axios.post(getmysqlUrl, params).then(res => res.data)
}

export function delmysql(params) {
    return Axios.delete(getmysqlUrl, { data: params }).then(res => res.data)
}

export function upmysql(params) {
    return Axios.put(getmysqlUrl, params).then(res => res.data)
}

export function gethome(info) {
    return Axios.get(`${gethomeUrl}?info=${info}`).then(res => res.data)
}

export function getsoft(nid) {
    return Axios.get(`${getsoftUrl}?nid=${nid}`).then(res => res.data)
}

export function upsoft(params) {
    return Axios.put(getsoftUrl, params).then(res => res.data)
}



